<template>

  <div class="hom">
   <div class="input">
     <input type="text" v-model="value" placeholder="请输入快递单号" @focus="isShowHistory=true" @blur="cancelHistory">
     <div class="close" @click="value=''"></div>
     <div class="search" @click="searchFn"></div>
     <div class="history" v-if="isShowHistory&&expressCode.length>0">
     <div class="historyItem" @click="chooseExpressCode(code)" v-for="code,index in expressCode" :key="index">{{code}}</div>
   </div>
   </div>
   
   <div class="searchResult">

    <h3>搜索结果</h3>
    <div class="content" v-if="data.length>0">
      <div class="header">
        <div class="time">
          <div class="text">时间</div>
          <div class="icon" @click="sort=!sort">
           <span class="topT" :class="{active:sort}">

           </span>
           <span class="bottomT" :class="{active:!sort}"></span>
          </div>
        </div>
        <div class="addr">地点和跟踪进度</div>
      </div>
        <div class="list">
          <div class="listItem" v-for="item,i in showlist" :key="i">
            <div class="liTime">
              <div class="data">{{item.time.split(' ')[0]}}</div>
              <div class="hour">{{item.time.split(' ')[1]}}</div>
            </div>
            <div class="midStyle">
               <div class="circle" :class="{active:(sort&&i==0)||((!sort)&&(i==showlist.length-1))}"></div>
              <div class="line"></div>
            </div>
            <div class="addrTxt">
             {{item.status}}
            </div>
          </div>
         
        </div>
    </div>
    <div v-else>
      <h5 style="color:#ccc;">尚无搜索结果</h5>
    </div>
    
   </div>
  </div>
  
  
</template>

<script>
// @ is an alias to /src

import axios from 'axios'
export default {
  name: 'Hom',
  data:function(){
     return {
    value:'DPK391001645810',
   
    data:[],
    sort:true,
    isShowHistory:false,
    expressCode:[]
  }
  },
  mounted () {
     this.expressCode = localStorage.expressCode?JSON.parse(localStorage.expressCode) : [];

  },
  computed:{
    showlist:function(){
      if(this.sort){
        return [...this.data]
     
      }
      else{
        return [...this.data].reverse()
      }
    }
  },

 methods:{
     
   async searchFn(){
     let httpUrl = `https://qyexpress.market.alicloudapi.com/composite/queryexpress?number=${this.value}`
     let config={
       headers:{
        'Authorization':'APPCODE 1ed344a7a8424a65989f7b69b0fb5ae9'
       }
     }
     let res = await axios.get(httpUrl,config)
     console.log(res)
     this.data = res.data.data.list
    //将搜素记录保存到本地存储
    this.saveExpressHistory()
      console.log(this.data)
     
   },
   saveExpressHistory(){
     //首先从本地存储里查看是否存在快递单号，如果有就解析出来，如果没有就创建一个数组
      this.expressCode = localStorage.expressCode?JSON.parse(localStorage.expressCode) : []

     //判断数组是否已存在数据
     if(this.expressCode.indexOf(this.value) == -1){
        this.expressCode.push(this.value)
        localStorage.expressCode = JSON.stringify(this.expressCode)

     }
    
   },
   chooseExpressCode (code) {
     this.isShowHistory=false
     this.value = code
     this.searchFn()
   },
   cancelHistory(){
     setTimeout(()=>this.isShowHistory=false,50)
   }
   
 }
}
</script>

<style lang="scss"> 
*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
.history{
  position: absolute;
  left: 0;
  top: 00.48rem;
  width: 3.55rem;
  display: flex;
  flex-direction: column;
  box-shadow: 0 2px 6px #ccc;
 
  background-color: #fff;
  .historyItem{
    width: 3.55rem;
    height: 0.5rem;
    padding: 0 0.1rem;
    line-height: 0.5rem;
  
  }
}
html{
  font-size: calc(100vw / 3.75);
}
body{
  margin: 0;
  font-size: 0.16rem;
}
  @mixin background($url){
     background-image: url($url);
    background-size: 100% auto;
    background-repeat: no-repeat;
  }
  .hom{
   @include background('../assets/topbanner.png');
  
    display: flex;
    flex-direction: column;
    align-items: center;
    .input{
      margin-top: 92px;
      width: 3.55rem;
      height: 0.48rem;
      display: flex;
      justify-content: center;
      align-items: center;
      background-color: #fff;
      border-radius: 0.08rem;
      box-shadow: 0 4px 4px #ccc;
      position: relative;
      input{
        flex: 1;
        border: none;
        height: 80%;
        outline: none;
        margin-left: 0.1rem;
        font-size: 0.16rem;
        padding: 0.05rem;
      }
    .close{
      width: 0.24rem;
       height: 0.24rem;
       @include background("../assets/cha.png");
        background-size: 70% auto;
        padding: 0 0.04rem;
        border-right: 1px solid #ccc;

      }
    .search{
     width: 0.26rem;
      height: 0.26rem;
      margin: 0 0.04rem;
      @include background('../assets/sousou.png')
   }
      }
    }
    @mixin triangleT($color:rgba(200, 214, 219, 0.692),$width:0.05rem){
      width: 0;
      height: 0;
      border-top: $width solid transparent;
    border-bottom: $width solid $color;
     border-left: $width solid transparent;
      border-right: $width solid transparent;
    }
    @mixin triangleBottom($color:rgba(200, 214, 219, 0.692),$width:0.05rem){
      width: 0;
      height: 0;
      border-top: $width solid $color;
    border-bottom: $width solid transparent;
     border-left: $width solid transparent;
      border-right: $width solid transparent;
    }
    .searchResult{
      margin-top: 0.3rem;
     .content{
       .header{
         width: 100vw;
         display: flex;
         font-size: 0.12rem;
         justify-content: flex-start;
        .time{
          width: 0.96rem;
          height: 0.2rem;
          display: flex;
          justify-content: center;
          .icon{
            display: flex;
            width: 0.2rem;
            flex-direction: column;
            justify-content: center;
            align-items: center;
          .topT{
            @include triangleT;
            margin-bottom: 0.02rem;
          }
          .bottomT{
            @include triangleBottom;
          }
          .topT.active{
             @include triangleT(deepskyblue);
          }
          .bottomT.active{
             @include triangleBottom(deepskyblue);
          }
          }
         
        }
        .addr{
          flex: 1;
          padding-left: 0.3rem;
        }
       }
       .list{
         width: 100vw;
         height: auto;
         display: flex;
         flex-direction: column;

         .listItem{
           width: 100vw;
           min-height: 0.56rem;
           
           display: flex;
           font-size: 0.12rem;
           .liTime{
             width: 0.96rem;
             text-align: center;

           }
           .midStyle{
             width: 0.14rem;
             display: flex;
             flex-direction: column;
             justify-content: center;
             align-items: center;
             transform: translate(0,0.06rem);
             .circle{
               width: 0.1rem;
               height: 0.1rem;
               border: 0.02rem solid #efefef;
               background-color: #888;
               border-radius: 0.07rem;
              
             }
             .circle.active{
                background-color: deepskyblue;
             }
             .line{
               width: 1px;
               flex: 1;
               background-color: #eee;
             }
         
           }
           .addrTxt{
           padding-left: 0.2rem;
           flex: 1;
           height: auto;
           padding-bottom: 0.4rem;
         }
         }

       }
     }

    }
    h3{
      text-align: center;
      margin-bottom: 0.3rem;
    }
  
      
  
</style>
